<template>
    <div>
        <el-card shadow='never'>
            <div slot="header" class="clearfix">
                <span>官网banner管理</span>
                <el-select v-model="type" @change='getData' class='small' style="width:140px;margin-left:20px" size='small' placeholder="banner类型">
                    <el-option  label="PC" value="1">
                    </el-option>
                    <el-option  label="Moblie" value="2">
                    </el-option>
                </el-select>
                <el-button style="float:right" type="primary" size='small' @click='add_banner'>新增</el-button>
            </div>

            <div class='banner-list'>
                <el-card v-for='(item,index) in list' :key='index' class="box-card card" :body-style="{ padding: '0px' }">
                <div slot="header" class="clearfix">
                    <span>{{item.Title}}</span>
                    <el-button style="float: right; padding: 3px 0" @click='updateBanner(item.ID)' type="text">修改</el-button>
                </div>
                <img :src='item.Image' >
                <div class='show' v-if='!item.IsShow'>不展示</div>
                </el-card>
            </div>
        </el-card>


    </div>
</template>

<script>
import { mapState } from "vuex";
export default {
    name: "banner",
    data() {
        return {
            type:'1',
            list:[]
        };
    },
    computed: {

    },
    methods: {
        add_banner(){
            this.$router.push(`/add_banner`);
        },
        getData(){
            let params = {
                btype: this.type,
                pageNo: 0,
                pageSize: 10
            };
            this.$api("/admin/banner/list", params).then(res => {
                this.list = res.data.list;
            });
            
        },
        updateBanner(id){
            this.$router.push(`/add_banner?id=${id}`);
        }
    },
    created() {
        this.getData();
        // this.getFacilityDetail(16)
    }
};
</script>


<style lang='scss' scoped>
.card{
     width: 300px;
    height: 200px;
    position: relative;
    float: left;
    margin: 20px;
        img{
            width: 100%;
                display: block;
    height: 140px;
        }
    }
.show{
    position: absolute;
    top: 80px;
    /* width: 300px; */
    width: 100px;
    font-size: 20px;
    color: #666;
    left: 0;
    right: 0;
    margin: 0 auto;
    border: 1px solid;
    height: 100px;
    border-radius: 50%;
    text-align: center;
    line-height: 100px;

}    
</style>



